@import '../../constants/Colors.scss';
@import '../../constants/Breakpoints.scss';

.Modal {
  background: #fff;
  color: $color-font-1;
  position: fixed;
  left: 0;
  right: 0;
  top: 2vh;
  margin: auto;
  max-width: 90vw;
  box-shadow: 0 5px 10px 2px rgba(195, 192, 192, 0.5);
  padding: 1em;
  text-align: center;
  z-index: 10;
}
